@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Orbitron", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  color: #000000;
}
body {
  background: #000000;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
  transition: 0.5s ease;
  -o-transition: 0.5s ease;
  -webkit-transition: 0.5s ease;
}

#topSection {
  width: 100%;
  background-color: #a21514;
}

#navigation {
  width: 100%;
  background-color: #1e1e1e;
}

#bodylayout {
  width: 100%;
  background-image: linear-gradient(#000000, #a21515);
}

.linespace {
  width: 100%;
  height: 6px;
  background-color: #333;
}

.container {
  position: relative;
  text-align: center;
  width: 1010px;
  margin-left: auto;
  margin-right: auto;
}

.containerContent {
  width: 100%;
}

.containerLayout {
  display: -webkit-flex;
  display: flex;
  position: relative;
  background: url(data:image/png;base64,) 0 0 repeat-x #fff;
  width: 100%;
  box-shadow: 0 5px 5px #000;
  padding-bottom: 30px;
}

.content {
  float: left;
  width: 75%;
}

.ads-horizontal,
.ads-vertical,
.ads-vertical-home,
.ads-featured-section {
  z-index: 3;
}
.ads-horizontal {
  float: right;
  margin-top: 20px;
  margin-right: 2%;
  width: 23%;

  z-index: 999;
  height: 500px;
}

.ads-vertical {
  float: center;
  width: 90%;
  margin-left: 5%;
  z-index: 999;
}

.ads-vertical-home {
  width: 700px;
  height: 200px;
  margin-left: 4%;
  margin-top: 20px;
  border-top: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
  border-right: 1px solid #dadada;
}

.ads-featured-section {
  content: "";
  height: 320px;
}
.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}

h1 {
  color: black;
}

/* Tab Css*/
.tabContainer {
  display: block;
  padding-top: 20px;
}

.card {
  width: 33%;
  display: inline-block;
  position: relative;
}

.cardImg {
  width: 232px;
  height: 165px;
  -o-box-sizing: border-box;
  box-sizing: border-box;
  margin-top: 4px;
  margin-bottom: 16px;
  object-fit: cover;
}

.cardText h4,
p {
  font-family: Tahoma, Verdana, Segoe, sans-serif;
  color: black;
  text-align: left;
  margin-left: 10px;
}
.cardText h4 {
  margin-top: -7px;
  font-size: 15px;
}

.cardText p {
  margin-top: 5px;
  font-size: 12px;
}

.cardBtn {
  float: left;
  margin-left: 10px;
  font: bold 12px/21px "Orbitron", sans-serif;
  padding: 4px 12px;
  text-transform: uppercase;
  color: #fff;
  background: url(data:image/png;base64,) 0 0 repeat-x #930706;
  border-bottom: 1px solid #c40a0a;
  border-radius: 5px 5px 0 0;
  margin-bottom: 5px;
  position: relative;
  margin-top: 10px;
}

.cardBtn:after {
  display: block;
  bottom: -6px;
  background: #373636;
  height: 5px;
  left: 0;
  border-radius: 0 0 5px 5px;
  right: 0;
  position: absolute;
  content: "";
}

