.footer li:before {
  content: "";
  border-color: transparent #6d6d6d;
  border-style: solid;
  border-width: 0.35em 0 0.35em 0.45em;
  display: block;
  height: 0;
  width: 0;
  left: -1em;
  top: 15px;
  position: relative;
}

.footer .current a {
